.share {
  width: 100%;
  height: 170px;
  border-radius: 10px;
  -webkit-box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.37);
  box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.37);
}
.shareTop {
  display: flex;
}
.shareWrapper {
  padding: 10px;
}
.shareProfileImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.shareInput {
  border: none;
  width: 80%;
}
.shareInput:focus {
  outline: none;
}
.shareHr {
  margin: 20px;
}
.shareBottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.shareOptions {
  display: flex;
  margin-left: 20px;
}
.shareOption {
  display: flex;
  align-items: center;
  margin-right: 15px;
  cursor: pointer;
}
.shareIcon {
  font-style: 18px;
  margin-right: 3px;
}
.shareOptionText {
  font-style: 14px;
}
.shareButton {
  border: none;
  cursor: pointer;
  padding: 7px;
  border-radius: 5px;
  background-color: rgb(223, 139, 139);
  margin-right: 20px;
  color: white;
}
